import React, { useState, useMemo } from "react";

// 
interface CsvDataItemTableProps {
  anyDataTags: string[];
  onSelectedTagsChange: (
    selectedData: { tag: string; index: number }[]
  ) => void; // 
}

// 
/**
 * 
 * @param {boolean} checked - 
 * @param {function} onChange - 
 */
const Checkbox = ({
  checked,
  onChange,
}: {
  checked: boolean;
  onChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
}) => (
  <input
    type="checkbox"
    checked={checked}
    onChange={onChange}
    className="form-checkbox h-5 w-5 text-gray-600"
  />
);

// 
/**
 * 
 * @param {string[]} anyDataTags - 
 * @param {function} onSelectedTagsChange - 
 */
const CsvDataItemTable: React.FC<CsvDataItemTableProps> = ({
  anyDataTags,
  onSelectedTagsChange,
}) => {
  const tagTranslations: { [key: string]: string } = {
    "gml:id": "GML ID",
    "gml:posList": "",
    "bldg:measuredHeight": "",
    "xAL:LocalityName": "",
  };

  const [selectedData, setSelectedData] = useState<
    { tag: string; index: number }[]
  >([]);
  // const [attributeNames, setAttributeNames] = useState<string[]>(
  //   new Array(anyDataTags.length).fill("")
  // );

  // anyDataTagsselectedData
  // useMemo(() => {
  //   setSelectedData([]);
  //   setAttributeNames(new Array(anyDataTags.length).fill(""));
  //   onSelectedTagsChange([]);
  // }, [anyDataTags]);

  // 
  /**
   * 
   * @param {string} tag - 
   * @param {boolean} isChecked - 
   * @param {number} index - 
   */
  const handleCheckboxChange = ({
    tag,
    isChecked,
    index,
  }: {
    tag: string;
    isChecked: boolean;
    index: number;
  }) => {
    let updatedSelectedData = [...selectedData];
    if (isChecked) {
      updatedSelectedData.push({
        tag,
        index,
      });
    } else {
      updatedSelectedData = updatedSelectedData.filter(
        (data) => data.tag !== tag
      );
    }
    setSelectedData(updatedSelectedData);
    onSelectedTagsChange(updatedSelectedData);
  };

  return (
    <div
      className="shadow overflow-hidden border-b border-gray-200 sm:rounded-lg"
      style={{ maxHeight: "260px", overflowY: "auto" }}
    >
      <table className="min-w-full divide-y divide-gray-200">
        <thead
          className="bg-gray-50"
          style={{
            position: "sticky",
            top: 0,
            zIndex: 1,
            backgroundColor: "#f9fafb",
          }}
        >
          <tr>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              
            </th>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              
            </th>
          </tr>
        </thead>
        <tbody className="bg-white divide-y divide-gray-200">
          {anyDataTags.map((tag, index) => (
            <tr key={index}>
              <td className="px-6 py-4 whitespace-nowrap">
                <Checkbox
                  checked={selectedData.some((data) => data.tag === tag)}
                  onChange={(e) =>
                    handleCheckboxChange({
                      tag,
                      isChecked: e.target.checked,
                      index,
                    })
                  }
                />
              </td>
              <td className="px-6 py-4 whitespace-nowrap">
                {tagTranslations[tag] || tag}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default CsvDataItemTable;
