import React, { useState } from 'react';


interface TagsComboBoxProps {
  tags: string[]; // 
  selectedTag: string; // 
  onTagSelected: (tag: string) => void; // 
}

const tagTranslations: { [key: string]: string } = {
  "gml:id": "GML ID",
  "gml:posList": "()",
  "bldg:measuredHeight": "(m)",
  "xAL:LocalityName": "()",
};

/**
 * TagsComboBox
 * 
 * 
 * 
 * @param {TagsComboBoxProps} props - 
 * @param {string[]} props.tags - 
 * @param {string} props.selectedTag - 
 * @param {function} props.onTagSelected - 
 * 
 * @returns {JSX.Element} - TagsComboBoxJSX
 */
const TagsComboBox: React.FC<TagsComboBoxProps> = ({ tags, selectedTag, onTagSelected }) => {
  const [currentTag, setCurrentTag] = useState<string>(selectedTag);

  const handleTagSelected = (tag: string) => {
    onTagSelected(tag);
    setCurrentTag(tag);   
  };

  return (
    <div className="mb-4">
      <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="tag-select">
        
      </label>
      <select 
        value={currentTag} 
        onChange={(e) => handleTagSelected(e.target.value)} 
        id="tag-select" 
        className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline" 
        size={4}
      >
        {tags.map((tag, index) => (
          <option title={tag} key={index} value={tag}>
            {tagTranslations[tag] || tag}
          </option>
        ))}
      </select>
      <div className="mt-4">
        <h3 className="text-gray-700 text-sm font-bold mb-2">:</h3>
        <p className="text-gray-700 text-sm">
          {tagTranslations[currentTag] || currentTag}
        </p>
      </div>
    </div>
  );
};

export default TagsComboBox;
